.bgimage{
    min-height: 570px;
    opacity: 0.9;
    background-color: #000;
}
.carousel-inner img{
    width: 100%;
    height: 100%;
}
.carousel-caption{
    margin-top: -40px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.carousel-caption h1{
    font-size: 500%;
    text-transform: uppercase;
    text-shadow: 1px 1px 10px #000;
}

.jumbotron-text{
    font-size: 25px;
    text-align: center;
    padding-top: 5vh;
    padding-bottom: 5vh;
}
.btnMore{
    display: flex;
    justify-content: center;
    align-items: center;

}



